import React from "react";
import headshot from '../imgs/headshot.png';

function About() {
  return (
    <div className="about">
      <div class="container pt-5">
        <div class="row align-items-center my-5">

          <div class="col-lg-7">
            <img
              class="img-fluid rounded mb-4 mb-lg-0"
              src={headshot}
              alt=""
            />
          </div>

          <div class="col-lg-5">
            <h1 class="font-weight-light">About</h1>
            <p>
              My Name is Mohammad Awwad I am a Software Engineer team lead on FRC 
              Robotics teams 6390 Hephaestus. I am a pationate Programmmer and love
              working with others. 
            </p>
          </div>

          <div class="col-lg-5">
            <a
              className="My-Github"
              href="https://github.com/mohammadawwad"
              target="_blank" 
              rel="noopener noreferrer"
            >
              Github
            </a>
          </div>
          

        </div>
      </div>
    </div>
    
  );
}

export default About;